/* Google fonts */

@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@500&display=swap');



/* Rest code */
*{
 margin: 0px;
 padding: 0px;
}



/* Navigatin sectin style  */
.useforFont{
  font-family: 'Dosis', sans-serif;
}

 .homePageDefault{
   border-bottom: 2px solid #E74C3C;
 }


 /* Slider Section style */

.sliderWrap{
  position: relative;

}

 .sliderMain{
   width: 100%;
   height: 900px;
  
 }

 .sliderHighLight{

  height: 200px;
  background-color: #FFFFFF; 
  position: absolute;
  left: 0px;
  right: 0px;
  margin: auto;
  bottom: 0px;
  top: 830px;
 }



 /* Vide Section Wrap */

 .videSectionwrap{
   margin-top: 180px;
 }




 .videoPlaySection{
  width: 500px;
  height:400px;
  margin-top: 40px;
 position: relative;
 }


 .videoPlay{
  width: 90px;
  height: 103px;
  cursor: pointer;
  position: absolute;
  left: 0px;
  right: 0px;
  bottom: 0px;
  top: 0px;
  margin: auto;
 }

 .videoTitle{
  width: 150px;
  height: 20px;  
  position: absolute;
  right: 0px;
  bottom: 0px;
  margin: 3px;
  text-transform: uppercase;
  font-size: 12px;
 }

 .exploreContent{
   background-color: #E74C3C;
   margin-top: 100px;
 }






/* On screens that are 700px wide or less, the background color is olive */
@media screen and (max-width: 700px) {

  .middleBox{
    display: none;
  }
  .lastBody{
   margin-top: 35px; 
  }
  .videoPlaySection{
    width: 330px;
    height:330px;
    margin: auto;
    margin-top: 30px;

   }
   .homePageDefault{
    border: none;
  }
}


